export default {
  name: 'editPass_div',
  data() {
    return {
      password: "",
      repassword: "",
      oldPassword: "",
      pwdType1: "password",
      pwdType2: "password",
      pwdType: "password"
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    this.$store.commit('changeTitle', '');
    this.$store.commit('changeBtn', ' ');
    this.$store.commit('isShow', ' ');
    // this.$nextTick(function () {
    //   this.username = this.$route.query.username;  
    // })
  },
  watch: {

  },
  methods: {
    edit() {
      var _this = this;
      if (this.password != this.repassword) {
        this.$vux.toast.show({
          text: '',
        })
        return false;
      }
      this.$post('/reset-password.do?passwordOld=' + this.oldPassword + '&passwordNew=' + this.password).then(response => {
        if (response.status === 0) {

          this.$vux.toast.show({
            text: '',
          })
          setTimeout(function() {
            _this.$router.push('/home')
          }, 800)
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    },
    changeType(status) {
      switch (status) {
        case 0:
          this.pwdType = this.pwdType === 'password' ? 'text' : 'password'
          break;
        case 1:
          this.pwdType1 = this.pwdType1 === 'password' ? 'text' : 'password'
          break;
        case 2:
          this.pwdType2 = this.pwdType2 === 'password' ? 'text' : 'password'
          break;
      }
    }
  }
}




