export default {
  name: 'detailEdit',
  data() {
    return {
      username: "",
      phone: "",
      role: [],
      list: [],
      groupCodeList: [],
      category: "",
      spec: "",
      unit: "",
      siteLeader:[],
      buyer:[],
      projectData: {
        "projectName": "",
        "issuingContract": "",
        "address": "",
        "startDate": "",
        "endDate": "",
        "content": "",
        "siteLeader": "aaa",
        "buyer": "",
        "receivableAmount": "",
        "budget": "",
        "id": 0
      },
      siteList:[],
      buyerList:[],
      hackReset: false,
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    this.onloadEdit();
  },
  watch: {

  },
  methods: {
    onloadEdit() {
      var _this = this;
      this.$store.commit('isShow', ' ');
      switch (this.$route.params.from) {
        case 'usergroup':
          this.username = this.$route.params.data.username;
          this.phone = this.$route.params.data.phone;
          this.role.push(this.$route.params.data.usergroup);
          this.$store.commit('changeTitle', '');
          this.$store.commit('changeBtn', ' ');
          this.$post('/list-user-group.do').then(response => {
            this.groupCodeList = response.data;
            var list1 = [];
            $.each(response.data, function(index, val) {
              var listItem = {};
              listItem['name'] = val.name;
              listItem['value'] = val.name;
              list1.push(listItem);
            });
            this.list.push(list1);
          })
          break;
        case 'materialControl':
          this.category = this.$route.params.data.category;
          this.spec = this.$route.params.data.spec;
          this.unit = this.$route.params.data.unit;
          this.$store.commit('changeTitle', '');
          this.$store.commit('changeBtn', ' ');
          break;
        case 'equipmentControl':
          this.category = this.$route.params.data.category;
          this.spec = this.$route.params.data.spec;
          this.unit = this.$route.params.data.unit;
          this.$store.commit('changeTitle', '');
          this.$store.commit('changeBtn', ' ');
          break;
        case 'project':
          this.projectData = this.$route.params.data;
          this.siteLeader.push(this.projectData.siteLeader);
          this.buyer.push(this.projectData.buyer);
          this.$store.commit('changeTitle', '');
          this.$store.commit('changeBtn', ' ');
          this.hackReset = false
          this.$nextTick(() => {
              this.hackReset = true
            })
          this.$fetch('/project/user-list.do').then(response => {
            var list1 = [];
            $.each(response.data.Site, function(index, val) {
              var listItem = {};
              listItem['name'] = val.name;
              listItem['value'] = val.name;
              list1.push(listItem);
            });
            this.siteList.push(list1);
            var list2 = [];
            $.each(response.data.Buyer, function(index, val) {
              var listItem = {};
              listItem['name'] = val.name;
              listItem['value'] = val.name;
              list2.push(listItem);
            });
            this.buyerList.push(list2);

          })
          break;
      }

    },
    editSubmit() {
      var _this = this;
      switch (this.$route.params.from) {
        case 'usergroup':
          let usergroupCode = "";
          let usergroup = _this.role[0];
          $.each(this.groupCodeList, function(index, val) {
            if (val.name == usergroup) {
              usergroupCode = val.value * 1;
            }
          });
          this.$post('/update-user.do', {
            "id": _this.$route.params.data.id,
            "usergroup": usergroup,
            "usergroupCode": usergroupCode
          }).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'userGroup'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.data,
              })
            }
          });
          break;
        case 'materialControl':
          this.$post('/platform/material/update.do', {
            "id": _this.$route.params.data.id,
            "category": _this.category,
            "spec": _this.spec,
            "unit": _this.unit
          }).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'materialControl'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.data,
              })
            }
          });
          break;
        case 'equipmentControl':
          this.$post('/platform/equipment/update.do', {
            "id": _this.$route.params.data.id,
            "category": _this.category,
            "spec": _this.spec,
            "unit": _this.unit
          }).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'equipmentControl'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.data,
              })
            }
          });
          break;
        case 'project':
          var re = /^[0-9]+.?[0-9]*$/; 
          if (!re.test(_this.projectData.receivableAmount) && !re.test(_this.projectData.budget)) {
            alert("");
            break;
          }
          _this.projectData.siteLeader = _this.siteLeader[0];
          _this.projectData.buyer = _this.buyer[0];
          _this.projectData.updateTime = null;
          this.$post('/project/update.do', _this.projectData).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'project'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.data,
              })
            }
          });
          break;
      }
    },
    delectSubmit() {
      var _this = this;
      switch (this.$route.params.from) {
        case 'usergroup':
          this.$post('/delete-user.do?userID=' + _this.$route.params.data.id).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'userGroup'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.data,
              })
            }
          });
          break;
        case 'materialControl':
          this.$post('/platform/material/delete.do?materialID=' + _this.$route.params.data.id).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'materialControl'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.data,
              })
            }
          });
          break;
        case 'equipmentControl':
          this.$post('/platform/equipment/delete.do?equipmentID=' + _this.$route.params.data.id).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'equipmentControl'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.data,
              })
            }
          });
          break;
        case 'project':
          this.$post('/project/update.do', {
            "id":  _this.$route.params.data.id, //! id
            "status": "delete" //delete
          }).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'project'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.data,
              })
            }
          });
          break;
      }
    }

  }
}




