export default {
  name: 'home_div',
  data() {
    return {
      newsNum: "2",
      back: false,
      Name: "",
      btn: ""
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    // this.$store.commit('changeBtn', 'home');
  },
  computed: {
    count() {
      return this.$store.state.showBack
    },
    titleContent() {
      return this.Name = this.$store.state.titleName
    },
    btnContent() {
      return this.btn = this.$store.state.rightBtn;
    }
  },
  watch: {
    count(val) {
      this.back = val;
    },
    titleContent(val) {
      this.Name = val;
    },
    btnContent(val) {
      this.btn = val;
    }
  },
  methods: {
    test() {
      let view = this.$store.state.rightView;
      if (view == 'userGroup') {
        this.$router.push({
          name: 'addNew',
          params: {
            from: 'user'
          }
        });
      } else if (view == 'materialControl') {
        this.$router.push({
          name: 'addNew',
          params: {
            from: 'material'
          }
        });
      } else if (view == 'equipmentControl') {
        this.$router.push({
          name: 'addNew',
          params: {
            from: 'equipment'
          }
        });
      } else if (view == 'setting') {
        this.$router.push({
          name: 'Edit'
        });
      } else if (view == 'estItem') {
        this.$router.push({
          name: 'estItemAdd'
        });
      } else if (view == 'addOrder') {
        this.$router.push({
          name: 'addOrder'
        });
      } else if (view == 'project') {
        this.$router.push({
          name: 'addNew',
          params: {
            from: 'project'
          }
        });
      }
    }
  }
}




