export default {
  name: 'addNew',
  data() {
    return {
      username: "",
      phone: "",
      role: [],
      list: [],
      groupCodeList: [],
      category: "",
      spec: "",
      unit: "",
      from: "",
      siteLeader: [],
      buyer: [],
      projectData: {
        "projectName": "",
        "issuingContract": "",
        "address": "",
        "startDate": "",
        "endDate": "",
        "content": "",
        "siteLeader": "",
        "buyer": "",
        "receivableAmount": "",
        "budget": ""
      },
      siteList:[],
      buyerList:[]
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    this.onloadAdd();
  },
  watch: {

  },
  methods: {
    onloadAdd() {
      var _this = this;
      this.$store.commit('isShow', ' ');
      if (this.$route.query.from) {
        this.from = this.$route.query.from;
      } else {
        this.from = this.$route.params.from;
      }
      switch (this.from) {
        case 'user':
          this.$store.commit('changeTitle', '');
          this.$store.commit('changeBtn', ' ');
          this.$post('/list-user-group.do').then(response => {
            this.groupCodeList = response.data;
            var list1 = [];
            $.each(response.data, function(index, val) {
              var listItem = {};
              listItem['name'] = val.name;
              listItem['value'] = val.name;
              list1.push(listItem);
            });
            this.list.push(list1);
          })
          break;
        case 'material':
          this.$store.commit('changeTitle', '');
          this.$store.commit('changeBtn', ' ');
          break;
        case 'equipment':
          this.$store.commit('changeTitle', '');
          this.$store.commit('changeBtn', ' ');
          break;
        case 'project':
          this.$store.commit('changeTitle', '');
          this.$store.commit('changeBtn', ' ');
          this.$fetch('/project/user-list.do').then(response => {
            var list1 = [];
            $.each(response.data.Site, function(index, val) {
              var listItem = {};
              listItem['name'] = val.name;
              listItem['value'] = val.name;
              list1.push(listItem);
            });
            this.siteList.push(list1);
            var list2 = [];
            $.each(response.data.Buyer, function(index, val) {
              var listItem = {};
              listItem['name'] = val.name;
              listItem['value'] = val.name;
              list2.push(listItem);
            });
            this.buyerList.push(list2);
          })
          break;
      }
    },
    addSubmit() {
      var _this = this;
      if (this.$route.query.from) {
        this.from = this.$route.query.from;
      } else {
        this.from = this.$route.params.from;
      }
      switch (this.from) {
        case 'user':
          let usergroupCode = "";
          let usergroup = _this.role[0];
          $.each(this.groupCodeList, function(index, val) {
            if (val.name == usergroup) {
              usergroupCode = val.value * 1;
            }
          })
          this.$post('/register.do', {
            "username": _this.username,
            "phone": _this.phone,
            "password": "888888",
            "usergroup": usergroup,
            "usergroupCode": usergroupCode
          }).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.msg,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'userGroup'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.msg,
              })
            }
          })
          break;
        case 'material':
          this.$post('/platform/material/add.do', {
            "category": _this.category,
            "spec": _this.spec,
            "unit": _this.unit
          }).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.msg,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'materialControl'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.msg,
              })
            }
          })
          break;
        case 'equipment':
          this.$post('/platform/equipment/add.do', {
            "category": _this.category,
            "spec": _this.spec,
            "unit": _this.unit
          }).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.msg,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'equipmentControl'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.msg,
              })
            }
          })
          break;
        case 'project':
          var re = /^[0-9]+.?[0-9]*$/; // // /^[1-9]+[0-9]*]*$/ 
          if (!re.test(_this.projectData.receivableAmount) && !re.test(_this.projectData.budget)) {
            alert("");
            break;
          }
          _this.projectData.siteLeader = _this.siteLeader[0];
          _this.projectData.buyer = _this.buyer[0];
          this.$post('/project/add.do', _this.projectData).then(response => {
            if (response.status === 0) {
              this.$vux.toast.show({
                text: response.msg,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'project'
                })
              }, 800)
            } else {
              this.$vux.toast.show({
                text: response.msg,
              })
            }
          })
          break;
      }
    },


  }
}




