export default {
  name: 'index_div',
  data() {
    return {
      logo_url: "../../static/img/logo.jpg",
      username: "",
      functionList:[]
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    this.onloadIndex()
  },
  watch: {

  },
  methods: {
    onloadIndex() {
      this.$store.commit('isShow', 'home');
      this.$store.commit('changeTitle', 'GDGS')
      this.$store.commit('changeBtn', 'home');
      

      this.$post('/get-user-info.do').then(response => {
        if (response.status === 0) {
            this.username = response.data.username;
            this.logo_url = response.data.headimgurl;
            this.groupList(response.data.usergroupCode)
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    },
    groupList(group){
      switch (group){
        case 0:
          var functionItem = {};
          functionItem['className'] = 'icon-usergroup';
          functionItem['label'] = ''
          functionItem['url'] = '/userGroup'
          var functionItem1 = {};
          functionItem1['className'] = 'icon-cailiao';
          functionItem1['label'] = ''
          functionItem1['url'] = '/materialControl'
          var functionItem2 = {};
          functionItem2['className'] = 'icon-shebei';
          functionItem2['label'] = ''
          functionItem2['url'] = '/equipmentControl'
          var functionItem3 = {};
          functionItem3['className'] = 'icon-xiangmu';
          functionItem3['label'] = ''
          functionItem3['url'] = '/project'
          var functionItem4 = {};
          functionItem4['className'] = 'icon-icon-test';
          functionItem4['label'] = ''
          functionItem4['url'] = '/addNew?from=project'
          var functionItem5 = {};
          functionItem5['className'] = 'icon-delete';
          functionItem5['label'] = ''
          functionItem5['url'] = '/delectProject'
          var functionItem6 = {};
          functionItem6['className'] = 'icon-jilu';
          functionItem6['label'] = ''
          functionItem6['url'] = '/siteIndex'
          // var functionItem7 = {};
          // functionItem7['className'] = 'icon-hesuankemuleixing';
          // functionItem7['label'] = ''
          // functionItem7['url'] = '/equipmentControl'
          // var functionItem8 = {};
          // functionItem8['className'] = 'icon-jindu';
          // functionItem8['label'] = ''
          // functionItem8['url'] = '/equipmentControl'
          // var functionItem9 = {};
          // functionItem9['className'] = 'icon-xiangmu';
          // functionItem9['label'] = ''
          // functionItem9['url'] = '/equipmentControl'
          this.functionList.push(functionItem,functionItem1,functionItem2,functionItem3,functionItem4,functionItem5,functionItem6);
          break;
         case 1:
          var functionItem = {};
          functionItem['className'] = 'icon-usergroup';
          functionItem['label'] = ''
          functionItem['url'] = '/userGroup'
          var functionItem1 = {};
          functionItem1['className'] = 'icon-cailiao';
          functionItem1['label'] = ''
          functionItem1['url'] = '/materialControl'
          var functionItem2 = {};
          functionItem2['className'] = 'icon-shebei';
          functionItem2['label'] = ''
          functionItem2['url'] = '/equipmentControl'
          this.functionList.push(functionItem,functionItem1,functionItem2);
          break;
         case 3:
          var functionItem3 = {};
          functionItem3['className'] = 'icon-xiangmu';
          functionItem3['label'] = ''
          functionItem3['url'] = '/project'
          var functionItem4 = {};
          functionItem4['className'] = 'icon-icon-test';
          functionItem4['label'] = ''
          functionItem4['url'] = '/addNew?from=project'
          var functionItem5 = {};
          functionItem5['className'] = 'icon-delete';
          functionItem5['label'] = ''
          functionItem5['url'] = '/delectProject'
          this.functionList.push(functionItem,functionItem1,functionItem2,functionItem3,functionItem4,functionItem5);
          break;
        case 4:
          this.$router.push({
            name: 'siteIndex'
          });
          // var functionItem6 = {};
          // functionItem6['className'] = 'icon-jilu';
          // functionItem6['label'] = ''
          // functionItem6['url'] = '/equipmentControl'
          // var functionItem7 = {};
          // functionItem7['className'] = 'icon-hesuankemuleixing';
          // functionItem7['label'] = ''
          // functionItem7['url'] = '/equipmentControl'
          // var functionItem8 = {};
          // functionItem8['className'] = 'icon-jindu';
          // functionItem8['label'] = ''
          // functionItem8['url'] = '/equipmentControl'
          // var functionItem9 = {};
          // functionItem9['className'] = 'icon-xiangmu';
          // functionItem9['label'] = ''
          // functionItem9['url'] = '/equipmentControl'
          // this.functionList.push(functionItem6,functionItem7,functionItem8,functionItem9);
          break;
      }
    }
  }
}




