export default {
  name: 'orderDetail',
  data() {
    return {
      username: "",
      pageNum: 1,
      dataList: [],
      orderData: {},
      orderNo: "",
      popperShow: false,
      popperData: {},
      quantityAct:0
    }
  },
  components: {

  },
  filters: {
    formatPercent: function(value) {
      return value + "%"
    }
  },
  mounted: function() {
    this.onloadDetail();
  },
  watch: {

  },
  methods: {
    onloadDetail() {
      var _this = this;
      this.orderData = JSON.parse(sessionStorage.getItem('orderData') || "[]")
      this.orderNo = sessionStorage.getItem('orderNo');
      this.$store.commit('isShow', ' ');
      this.$store.commit('changeTitle', '')
      this.$store.commit('changeBtn', ' ');
      this.$post('/site-header/order-item-list.do', {
        "projectId": this.orderData.projectId,
        "orderNo": this.orderNo,
        "pageNum": this.pageNum,
        "pageSize": 10
      }).then(response => {
        if (response.status === 0) {
          this.dataList = this.dataList.concat(response.data.list)
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });

    },
    loadMore() {
      this.pageNum += 1;
      this.onloadDetail();
    },
    gotoConfirm(item) {
      this.popperData = item;
      this.popperShow = !this.popperShow;
    },
    cancel() {
      this.popperShow = !this.popperShow;
    },
    Confirm() {
      var postData = {
        "projectId": this.orderData.projectId,
        "orderNo": this.orderNo,
      };

      console.log(this.popperData)
      var data = {
        "id": this.popperData.id,
        "quantityAct": this.quantityAct
      }
      postData['projectOrderItemList'] = [data];


      this.$post('/site-header/confirm-order-item.do', postData).then(response => {
        if (response.status === 0) {
            this.$vux.toast.show({
              text: response.data,
            })
            var _this = this;
            _this.dataList = [];
            _this.onloadDetail()
            setTimeout(function(){
                 _this.popperShow = !_this.popperShow;

            },1000)
         
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    }

  }
}




