export default {
  name: 'estItem',
  data() {
    return {
      isLoad:true,
      noData: false,
      type: '2',
      list: [],
      pageNum:1,
      footer:{
        title:''
      }
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    this.onloadEstItem();
  },
  watch: {

  },
  methods: {
    onloadEstItem() {
      var _this = this;
      this.$store.commit('isShow', ' ');
      this.$store.commit('changeTitle', this.$route.query.projectName);
      this.$store.commit('changeBtn', 'estItem');
      var data  = {};
      data['projectId'] = this.$route.query.projectId;
      data['projectName'] = this.$route.query.projectName;
      sessionStorage.setItem('estItemData',JSON.stringify(data));

      this.$post('/site-header/est-item-list.do?',{
          "projectId":this.$route.query.projectId,
          "pageNum":_this.pageNum,
          "pageSize":10
      }).then(response => {
        if (response.status === 0) {
          this.isLoad = false;
          if(response.data.length == 0){
            this.noData = true;
          }else{
            $.each(response.data.list, function(index, val) {
                var userData = {};
                userData['title'] = val.spec;
                userData['desc'] = ''+val.category+'/&nbsp;&nbsp;&nbsp;&nbsp;'+val.quantityEst+'&nbsp;'+val.unit
                userData['meta'] = {};
                userData.meta['id'] = val.id;
                userData.meta['data'] = val;
                _this.list.push(userData)
            });
          }
          console.log(response.data)
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    },
    itemClick(item){
      // this.$router.push({
      //     name: 'detailEdit',
      //     params: {
      //       data: item.meta.data,
      //       from: 'materialControl'
      //     }
      //   })
    },
    loadMore(){
      this.pageNum +=1;
      this.onloadEstItem();
    }
  }
}




