export default {
  name: 'reg_div',
  data() {
    return {
      password: "",
      repassword: "",
      forgetToken: "",
      username: "",
      content: '', // 
      totalTime: 60, //
      canClick: true
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    this.$nextTick(function () {
      this.username = this.$route.query.username;  
    })
  },
  watch: {

  },
  methods: {
    revise() {
      this.username = this.$route.query.username;
      if (this.password != this.repassword) {
        this.$vux.toast.show({
          text: '',
        })
        return false;
      }
      this.$post('/forget-reset-password.do?phone=' + this.username + '&passwordNew=' + this.password + '&forgetToken=' + this.forgetToken ).then(response => {
        if (response.status === 0) {
          // this.$router.push('/home')
          this.$vux.toast.show({
            text: '',
          })
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    },
    getCode() {
      // this.username = this.$route.query.username;
      if (this.username != "") {
        this.$post( '/get-verify-code.do?phone=' + this.username ).then(response => {
          if (response.status === 0) {
            // this.$router.push('/home')
            this.$vux.toast.show({
              text: "",
            })
            if (!this.canClick) return //
            this.canClick = false
            this.content = this.totalTime + 's'
            let clock = window.setInterval(() => {
              this.totalTime--
              this.content = this.totalTime + 's'
              if (this.totalTime < 0) {
                window.clearInterval(clock)
                this.content = ''
                this.totalTime = 10
                this.canClick = true //
              }
            }, 1000)
          } else {
            this.$vux.toast.show({
              text: response.msg,
            })
          }

        })
      }else{
        
      }
    }
  }
}




