export default {
  name: 'siteIndex',
  data() {
    return {
      username:"",
      pageNum:1,
      projectList: [],
    }
  },
  components: {

  },
  filters: {
    formatPercent: function(value) {
      return value + "%"
    }
  },
  mounted: function() {
    this.onloadSite();
  },
  watch: {

  },
  methods: {
    onloadSite() {
      var _this = this;
      this.$store.commit('isShow', 'home');
      this.$store.commit('changeTitle', '')
      this.$store.commit('changeBtn', 'home');
      this.$post('/get-user-info.do').then(response => {
        if (response.status === 0) {
            this.username = response.data.username;
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
      this.$fetch('/site-header/project-list.do?pageNum='+_this.pageNum+'&pageSize=10').then(response => {
        if (response.status === 0) {
            this.projectList = this.projectList.concat(response.data.list)
        } else {
            this.$vux.toast.show({
              text: response.msg,
            })
        }

      })
    },
    toggleButton(item) {
      var _this = this;
      $.each(_this.projectList, function(index, val) {
        _this.projectList[index].show = false;
      });

      item.show = !item.show;
    },
    loadMore(){
      this.pageNum +=1;
      this.onloadSite();
    },
    hrefClick(status,item){
      switch (status) {
        case 0:
          this.$router.push({
            path: 'estItem',
            query: {
              projectId:item.id,
              projectName:item.projectName
            }
          });
          break;
        case 1:
          this.$router.push({
            path: 'orderList',
            query: {
              projectId:item.id,
              projectName:item.projectName,
              address:item.address
            }
          });
          break;
        case 2:
          var data  = {};
          data['projectId'] = item.id;
          data['projectName'] = item.projectName;
          data['progress'] = item.progress;
          sessionStorage.setItem('progressData',JSON.stringify(data));
          this.$router.push({
            path: 'updateProgress',
            query: {
              projectId:item.id,
              projectName:item.projectName,
              progress:item.progress
            }
          });
          break;
      }
    }


  }
}




