export default {
  name: 'userGroup',
  data() {
    return {
      isLoad:true,
      noData: false,
      type: '2',
      list: [],
      footer:{
        title:''
      },
      pageNum:1
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    this.onloadUser();
  },
  watch: {

  },
  methods: {
    onloadUser() {
      var _this = this;
      this.$store.commit('isShow', ' ');
      this.$store.commit('changeTitle', '');
      this.$store.commit('changeBtn', 'userGroup');
      this.$fetch('/list-user.do?pageNum='+_this.pageNum+'&pageSize=10').then(response => {
        if (response.status === 0) {
          this.isLoad = false;
          if(response.data.length == 0){
            this.noData = true;
          }else{
            $.each(response.data.list, function(index, val) {
                var userData = {};
                userData['title'] = val.username;
                userData['desc'] = ''+val.usergroup+'  '+val.phone;
                userData['meta'] = {};
                userData.meta['id'] = val.id;
                userData.meta['data'] = val;
                _this.list.push(userData)
            });
            
          }
          console.log(response.data)
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    },
    itemClick(item){
      this.$router.push({
          name: 'detailEdit',
          params: {
            data: item.meta.data,
            from: 'usergroup'
          }
        })
    },
    loadMore(){
      this.pageNum +=1;
      this.onloadUser();
    }
  }
}




