export default {
  name: 'delectDiv',
  data() {
    return {
      isLoad:true,
      noData: false,
      footer:{
        title:''
      },
      type: '2',
      list: [],
      pageNum:1
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    this.onloadProject();
  },
  watch: {

  },
  methods: {
    onloadProject() {
      var _this = this;
      this.$store.commit('isShow', ' ');
      this.$store.commit('changeTitle', '');
      this.$store.commit('changeBtn', ' ');
      this.$fetch('/project/list.do?pageNum='+_this.pageNum+'&pageSize=10&status=del').then(response => {
        if (response.status === 0) {
          this.isLoad = false;
          if(response.data.length == 0){
            this.noData = true;
          }else{
            $.each(response.data.list, function(index, val) {
                var userData = {};
                userData['title'] = val.projectName;
                userData['desc'] = ''+val.budget+'/   '+val.siteLeader
                userData['meta'] = {};
                userData.meta['id'] = val.id;
                userData.meta['data'] = val;
                _this.list.push(userData)
            });
          }
          console.log(response.data)
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    },
    itemClick(item){
      const _this = this;
      this.$vux.confirm.show({
        content:'',
        'confirm-text':'',
        'cancel-text':'',
        // show
        onCancel () {
          _this.$vux.confirm.hide()
        },
        onConfirm () {
          _this.$post('/project/update.do', {
            "id":  item.meta.data.id, 
            "status": "new added" 
          }).then(response => {
            if (response.status === 0) {
              _this.$vux.toast.show({
                text: response.data,
              })
              setTimeout(function() {
                _this.$router.push({
                  name: 'Home'
                })
              }, 800)
            } else {
              _this.$vux.toast.show({
                text: response.data,
              })
            }
          });
        }
      })
      // this.$router.push({
      //     name: 'detailEdit',
      //     params: {
      //       data: item.meta.data,
      //       from: 'project'
      //     }
      //   })
    },
    loadMore(){
      this.pageNum +=1;
      this.onloadProject();
    }
  }
}




