export default {
  name: 'login_div',
  data() {
    return {
      username: "",
      password: "",
      isShow: false
    }
  },
  components: {

  },
  filters: {},
  mounted: function() {
    if (this.$route.params.needLogin) {
      this.isShow = this.$route.params.needLogin;
    } else {
      this.$post('/get-user-info.do').then(response => {
        if (response.status === 0) {
          var _this = this;
          if (response.data.usergroupCode == 4) {
            setTimeout(function() {
              _this.$router.push('/siteIndex')
            }, 1000)
          } else {
            setTimeout(function() {
              _this.$router.push('/home')
            }, 1000)
          }
        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    }


  },
  watch: {

  },
  methods: {
    login() {
      this.$post('/login.do', {
        phone: this.username,
        password: this.password
      }).then(response => {
        if (response.status === 0) {
          this.$vux.toast.show({
            text: ''
          })
          var _this = this;
          if (response.data.usergroupCode == 4) {
            setTimeout(function() {
              _this.$router.push('/siteIndex')
            }, 1000)
          } else {
            setTimeout(function() {
              _this.$router.push('/home')
            }, 1000)
          }

        } else {
          this.$vux.toast.show({
            text: response.msg,
          })
        }
      });
    },
    forgetHref() {
      this.$router.push({ path: '/Forget', query: { username: this.username } })
    }
  }
}




